.dis-scroll {
    overflow: hidden;
    height: 100vh;
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
}

.popup-primary {
    --transition-time: 0.3s;
    position: fixed;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    z-index: 1051;
    background-color: rgba(34, 41, 102, 0.4);
    cursor: pointer;
    overflow-y: auto;
    overflow-x: hidden;
    opacity: 0;
    visibility: hidden;
    transition: opacity var(--transition-time), visibility var(--transition-time);
    text-align: center;

    &__container {
        position: relative;
        min-height: var(--vh);
        display: inline-block;
        vertical-align: middle;
        background-color: #fff;
        cursor: default;
        text-align: left;
        display: none;

        &.popup-open {
            display: inline-block !important;
        }
    }

    &__close {
        position: absolute;
        top: 40px;
        right: 40px;
        border: none;
        padding: 0;
        width: 36px;
        height: 36px;
        background-color: #C7C9CD;
        cursor: pointer;
        border-radius: 50%;
        display: flex;
        align-items: center;
        justify-content: center;

        transition: background-color $transitionSpeed $transitionFunction;

        svg {
            width: 16px;
            height: 16px;
            fill: none;
            stroke: var(--white);
            stroke-width: 3.5px;
            transition: stroke $transitionSpeed $transitionFunction;
            @media (max-width:$tablet-max){
                width: 12px;
                height: 12px;
            }
        }

        @media (min-width:$tablet-min) {
            &:hover {
                background-color: #a5a5a5;
            }
        }

        @media (max-width:$tablet-max) {
            top: 24px;
            right: 24px;
            width: 30px;
            height: 30px;
        }
    }

    &.is-open {
        opacity: 1;
        visibility: visible;
        transition: opacity var(--transition-time), visibility var(--transition-time);
    }

    &--show-qr {
        display: flex;
        align-items: center;
        justify-content: center;

        .popup-primary {
            &__container {
                min-height: auto;
                max-width: 910px;
                border-radius: 70px;
                padding: 100px 100px 82px 56px;

                @media (max-width:$tablet-max) {
                    border-radius: 24px 24px 0 0;
                    padding: 65px 24px 32px;
                    max-width: none;
                    width: 100%;
                    height: auto;
                }
            }
        }
        @media (max-width:$tablet-max){
            align-items: end;
        }
    }

}




.show-qr {
    display: flex;
    align-items: center;

    &__title {
        @include adaptiveValue("font-size", 24, 18, 1296, 576, 1);
        line-height: 120%;
        font-weight: 500;
        max-width: 297px;

        span {
            color: var(--blue);
            display: block;
            @media (max-width:$tablet-max){
                display: inline-block;
            }
        }
        @media (max-width:$tablet-max){
            text-align: center;
            max-width: none;
            margin-bottom: 24px;
        }
    }

    &__qrs {
        display: flex;
        margin-left: 18px;
        @media (max-width:$tablet-max){
            margin-left: 0;
        }
    }

    &__qr {
        &:not(:last-child) {
            margin-right: 18px;
            @media (max-width:$smallTablet-max){
                 margin-right: 8px; 
            }
        }

        span {
            display: block;
            margin-top: 15px;
            color: var(--black);
            @include adaptiveValue("font-size", 24, 18, 1296, 576, 1);
            font-weight: 500;
            line-height: 130%;
            text-align: center;
        }

        @media (max-width:$tablet-max) {
            max-width: 200px;
        }
    }
    @media (max-width:$tablet-max){
        flex-direction: column;
    }
}











.fade {
    opacity: 0;
}

.fade,
.fade.animate-open {
    transition: opacity var(--transition-time);
}

.fade.animate-open {
    opacity: 1;
}

.fadeInUp {
    opacity: 0;
    transform: translateY(-100px);
}

.fadeInUp,
.fadeInUp.animate-open {
    transition: transform var(--transition-time), opacity var(--transition-time);
}

.fadeInUp.animate-open {
    opacity: 1;
    transform: translateY(0);
}
