.crm-digitale-umgebung {
   @include adaptiveValue("margin-top", 96, 32,1296,576,1);

    &__container {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        grid-column-gap: 24px;
        grid-row-gap: 44px;

        @media (max-width:$tablet-max) {
            grid-template-columns: repeat(2, 1fr);
            grid-row-gap: 24px;
        }

        @media (max-width:$smallTablet-max) {
            grid-template-columns: 1fr;
            grid-row-gap: 16px;
        }
    }


    &__item {

        &:nth-child(1) {
            grid-column: 1/-1;

            display: grid;
            align-items: center;
            grid-column-gap: 62px;
            grid-template-columns: percent(745, 1296) percent(405,1296);

            .crm-digitale-umgebung__info {
                margin-bottom: 45px;
                @media (max-width:$smallTablet-max){
                     text-align: center;
                     margin-bottom: 74px; 
                }
                @media (max-width:$mobile-max){
                     margin-bottom: 52px; 
                }
            }

            .crm-digitale-umgebung__image {
                @include adaptiveValue("border-radius", 70, 24, 1296, 576, 1);
                background: linear-gradient(112deg, #348D5E -4.84%, #60C28E 64.36%);

                display: flex;
                justify-content: center;
                picture{
                    max-width: percent(625,745);
                    transform: translateY(-14.2%);
                }
                @media (max-width:$smallTablet-max){
                     order: 2; 
                }
            }
            .crm-digitale-umgebung__title{
                span{
                    color: var(--blue);
                }
            }
            .crm-digitale-umgebung__descr {
                margin-top: 32px;

                @include adaptiveValue("font-size", 18, 14, 1296, 576, 1);
                line-height: 160%;
                @media (max-width:$smallTablet-max){
                     margin-top: 16px; 
                }
            }
            @media (max-width:$tablet-max){
                grid-template-columns: percent(745, 1296) 1fr;
                grid-column-gap: 48px;
            }
            @media (max-width:$smallTablet-max){
                 grid-template-columns: 1fr; 
            }
        }

        &:nth-child(2),
        &:nth-child(3),
        &:nth-child(4) {
            padding: 47px 43px;

            background-color: #FBFBFB;
            border: 1px solid #E7EAF2;
            @include adaptiveValue("border-radius", 60, 24, 1296, 576, 1);
            display: flex;
            flex-direction: column;

            @media (max-width:$tablet-max) {
                padding: 20px;
            }

            .crm-digitale-umgebung__title {
                color: var(--dark);
        
                @include adaptiveValue("font-size", 24, 18, 1296, 576, 1);
                line-height: math.div(30, 24);
                font-weight: 500;
                span {
                    color: var(--blue);
                }
        
                &:last-child {
                    margin-top: auto;
                }
            }
        
        }


        &:nth-child(2) {
            .crm-digitale-umgebung__images {
                margin-top: auto;
            }
            .crm-digitale-umgebung__title{
                margin-bottom: 36px;
                @media (max-width:$tablet-max){
                    margin-bottom: 24px;
                }
            }
        }

        &:nth-child(3) {
            .crm-digitale-umgebung__images {
                @media (max-width:$smallTablet-max) {
                    width: 125px;
                    margin-bottom: 24px;
                }
            }
        }

        &:nth-child(4) {
            .crm-digitale-umgebung__elements {
                margin-top: auto;
            }
            .crm-digitale-umgebung__title{
                margin-bottom: 25px;
                @media (max-width:$tablet-max){
                    margin-bottom: 24px;
                }
            }
            @media (max-width:$tablet-max) {
                grid-column: 1/-1;
            }
        }
    }

    &__images {
        img {
            filter: drop-shadow(2.7px 4px 24px rgba(51, 60, 147, 0.06));
            border-radius: 24px;

            @media (max-width:$smallTablet-max) {
                width: 200px;
                border-radius: 16px;
            }
        }
    }

    &__elements {
        background: #DAE1FE;
        border-radius: 34px;
        padding: 16px 24px;
        overflow: hidden;

        .item-primary {
            &:first-child {
                position: relative;
                left: 53px;
            }

            &:not(:last-child) {
                margin-bottom: 10px;
            }
        }

        @media (max-width:$tablet-max) {
            align-self: start;
        }

        @media (max-width:$smallTablet-max) {
            border-radius: 16px;

            .item-primary {
                &:first-child {
                    left: 16px;
                }
            }
        }
    }

    &__btn {
        margin-top: 72px;
        align-self: center;

        @media (max-width:$tablet-max) {
            margin-top: 50px;
        }
    }
}
